* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  background-color: #FFFFFF;

}

.header-container {
  margin: 0 auto;
  padding: 0.75rem 1.2rem;
  box-shadow: inset 0 -5px 4px -6px rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav-logo {
  width: 40px;
  height: 40px;
  background-color: #FF8A00;
}

/* nav Bar */
.nav-items {
  display: flex;
  align-items: center;
  list-style-type: none;
  cursor: pointer;

}

.nav-links {
  background-color: #F4F4F4;
  padding: 5px 1.3rem;
  border-radius: 100px;
  transition: all 0.3s ease;

}


.nav-items>li {
  margin-right: 1rem;
  font-weight: bold;
  color: #492D13;
  border-bottom: 2px solid transparent;
  padding: 5px;

}

.nav-items>li:hover {
  color: #F29C52;
 
}

.nav-cart>.fa-solid {
  margin: 10px;
  font-size: 18px;
}

.nav-cart>.fa-solid {
  transition: all 0.2s ease;
  cursor: pointer;
}



.log-btn {
  border: none;
  outline: none;
  background-color: #F4F4F4;
  padding: 10px;
  border-radius: 10px;
}

.fa-circle {
  color: green;
}

/* body */
.body-container {
  background-color: #FFF5ED;
  padding: 1rem;
  margin-bottom: 2rem;
}

.buttons {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.btn {
  padding: 10px 1rem;
  border-radius: 20px;
  border: none;
  background-color: #F29C52;
  font-weight: bold;
  color: white;
  cursor: pointer;
  transition: all 0.2s ease;


}

.btn-primary {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.3px;
  color: white
}

.search-container {
  display: flex;
  align-items: center;
}

.text-input {
  padding: 8px;
  border: 2px solid #F7D5B7;
  border-radius: 10px 0 0 10px;
  background: linear-gradient(to right, #F8E5D3, #faf0e3);

}

.search-btn {
  border-radius: 0 10px 10px 0;
  padding: 8px;
  border: 2px solid #F7D5B7;
  cursor: pointer;
  background-color: #F7D5B7;
  color: #492d13;
  font-weight: bold;

}

/*  restro card styling */
.restro-container {
  margin-top: 1.2rem;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
  margin: 1.2rem;
}

.restro-card:hover {
  transform: scale(1.01);
}

.restro-card {
  width: 260px;
  height: 340px;
  background-color: #F4F4F4;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
  padding: 1rem;
  border-radius: 10px;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}

.restro-img {
  width: 100%;
  height: 150px;
  background-size: contain;
  border-radius: 10px;

}

/* restro descriptions */
.restro-heading {
  color: #492D13;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.3px;
  overflow-wrap: break-word;
}

.restro-desc {
  margin-top: 10px;
}

.restro-ratings>p {
  display: flex;
  align-items: center;

  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: -0.3px;
  color: rgba(2, 6, 12, 0.75);
  margin-top: 2px;
}

.restro-ratings {
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* margin: 10px 10px 0 10px ; */

}

.fa-star {
  background-color: green;
  font-size: 10px;
  border-radius: 100px;
  padding: 4px;
  text-align: center;
  margin-left: 5px;
  color: white;
}

.restro-time {
  margin-right: 10px;
}

.restro-cost {
  font-weight: bold;
  color: #F29C52;
  font-size: 16px;
  margin-top: 3px;
}

.restro-cusines {
  font-weight: 200;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: -0.3px;
  color: rgba(2, 6, 12, 0.6);
  overflow: hidden;
  width: 100%;
  overflow-wrap: break-word;
}

.fa-solid{
  size: 14px;
}
li > a{
  text-transform: none;
  text-decoration: none;
}


.shimmer-ui {

  margin-top: 10px;
  width: 260px;
  height: 300px;
  background-color: #bbbaba;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
  padding: 1rem;
  border-radius: 10px;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}

.error {
  position: absolute;
  top: 47%;
  text-align: center;
  color: red;
}


/* footer */
.footer-container {
  background-color: #492D13;
  color: white;
  position: fixed;
  padding: 1rem;
  bottom: 0;
  width: 100%;
  box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.1);
}